			_getDetailImg(list) {
				let image = '';
				let regx = /<[^>]*>|<\/[^>]*>/gm;
				let len = list.length;
				for (var i = 0; i < len; i++) {
					if (list[i].indexOf("<txt>") != -1) {
						image += "";
					} else {
						image += "<img src='https://" + list[i].replace(regx, "") + "' style='width:100%;max-width:100%' />";
					}
				}
				return image
			},
